*,
*::before,
*::after{
  box-sizing: border-box;
}
body{
  margin: 0;
}
.container{
  display: flex;
}
.img{
  backdrop-filter: blur(1px);
  margin: 0 auto;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 60vw;
  height: 100vh;
  color: rgb(245, 245, 245);
}
.contain{
  font-family: 'Roboto Slab', serif;
  padding: 1.25rem 2.5rem;
  margin: 0 auto;
  width:500px;
  display: flex;
  justify-content: center;
  align-items: center;
}
.contain div{
  margin: 0 auto;
}
.temp h1{
  font-size: 6.25rem;
}
.city h2{
  font-size: 3.438rem;
}
.icon{
  text-align: center;
}
.icon p{
  margin-top: -20px;
  font-weight: light;
  letter-spacing: 1.5px;
  font-size: 25px;
}
.pane{
  border: 1px solid black;
  background: transparent;
  backdrop-filter: blur(12px);
  width:40vw;
}
.pane{
  font-family: 'Raleway', sans-serif;
  font-weight: 100;
  color: rgba(255, 255, 255, 0.714);
  padding: 20px 20px;
  display: flex;
  flex-direction: column;
}
.pane h3{
  font-weight: 500;
  padding: 10px 10px 0px 0px;
}
input{
  color: white;
  font-size: 15px;
  width: 100%;
  height:40px;
  background-color: transparent;
  border: none;
  border-bottom: 1px solid white;
  margin-bottom: 10px;
}
input:focus{
  outline: none;
}

button {
  width: 100px;
  font-size: 16px;
  font-weight: 100;
  color: #fff;
  cursor: pointer;
  height: 30px;
  text-align:center;
  border: none;
  background-size: 300% 100%;

  border-radius: 50px;
  moz-transition: all .4s ease-in-out;
  -o-transition: all .4s ease-in-out;
  -webkit-transition: all .4s ease-in-out;
  transition: all .4s ease-in-out;
}
button:hover {
  background-position: 100% 0;
  moz-transition: all .4s ease-in-out;
  -o-transition: all .4s ease-in-out;
  -webkit-transition: all .4s ease-in-out;
  transition: all .4s ease-in-out;
}
button {
  background-image: linear-gradient(to right, #fc6076, #ff9a44, #ef9d43, #e75516);
    box-shadow: 0 4px 15px 0 rgba(252, 104, 110, 0.75);
}

.button:focus {
  outline: none;
}
.detail{
  width: 100%;
  display: flex;
  justify-content: space-between;
}

@media screen and (max-width: 840px) {
  .pane{
    backdrop-filter: none;
    border:none;
  }
  .container{
    flex-direction: column;
    backdrop-filter: blur(2px);
  }
  .img{
    height:40vh;
  }
  .contain{
    width: 100vw;
  }
  .city h2{
    font-size: 2.5rem;
  }
  .icon p{
    font-size: 1.5rem;
  }
  .pane{
    width:100vw;
  }
}